@import './base.scss';
/**created by Rey on 2018.05.30***/
.banner{
  .swiper-container{
    padding-bottom: 40%;
  }
}
.nav{
  justify-content: space-around;
  margin: .26rem 0;
  a{
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    width: 1.2rem;
    height: 1.2rem;
    border-radius: 50%;
    background: $red;
    color: #fff;
    font-size: .24rem;
  }
}
.product_group{
  padding: 0 .2rem;
  .list{
    margin-bottom: .45rem;
    border-radius: .1rem;
    overflow: hidden;
    .top{
      padding: 0 .1rem;
      font-size: .24rem;
      background: $red;
      color: #fff;
      .flex{
        justify-content: space-between;
        align-items: center;
      }
      span{
        i{
          font-size: .3rem;
          margin-left: .2rem;
        }
        b{
          font-weight: normal;
          margin-left: .1rem;
        }
      }
      a{
        font-size: .24rem;
        color: #fff;
      }
      .time{
        i{
          font-size: .4rem;
          margin-right: .2rem;
        }
        span{
          font-size: .3rem;
        }
      }
    }
    .group{
      padding: .1rem .05rem;
      background: #fff;
      li{
        float: left;
        width: 50%;
        padding: 0 .05rem;
        margin-bottom: .3rem;
        .img_box::before{
          padding-bottom: 102.94%;
        }
        .title{
          line-height: .5rem;
          a{
            font-size: .3rem;
            color: #666;
          }
          &.loca{
            position: relative;
            padding-right: .4rem;
            i{
              position: absolute;
              top: 0;
              right: .1rem;
              font-size: .34rem;
              color: #F39700;
            }
          }
        }
        .price{
          font-size: .24rem;
          color: #4D4D4D;
        }
      }
    }
  }
  .video_box{
    .name{
      font-size: .36rem;
      color: #3E3E3E;
      line-height: .8rem;
      font-weight: bold;
    }
    .video{
      &::before{
        padding-bottom: 50%;
      }
      i{
        position: absolute;
        left: 0;
        right: 0;
        top: 0;
        bottom: 0;
        width: 1rem;
        height: 1.88rem;
        margin: auto;
        z-index: 10;
        color: #fff;
        font-size: 1rem;
      }
    }
  }
}