/**created by Rey on 2018.06.01***/
@import './base.scss';
.shopcart{
  .main{
    padding: .25rem .20rem;
  }
  .viewed{
    margin-top: .5rem;
    justify-content: space-between;
    font-size: .24rem;
    font-weight: bold;
  }
  .viewed_group{
    background: #fff;
    flex-wrap: wrap;
    padding: .2rem 0;
    margin-top: .2rem;
    li{
      flex-shrink: 0;
      width: 50%;
      padding: .1rem;
    }
  }
}
.shop_group{
  li{
    padding: .3rem .08rem;
    margin-bottom: .27rem;
    .lf{
      width: 1.68rem;
      flex-shrink: 0;
      padding-top: .2rem;
      .bottom{
        margin-top: .45rem;
        justify-content: space-between;
        i{
          color: $red;
          font-size: .32rem;
          font-weight: bold;
        }
      }
    }
    .rt{
      width: 100%;
      overflow: hidden;
      position: relative;
      padding-right: .8rem;
      padding-left: .2rem;
      .check_box{
        position: absolute;
        top: .06rem;
        right: .3rem;
      }
      .name{
        font-size: .3rem;
        color: #4D4D4D;
        margin-bottom: .16rem;
      }
      .info{
        font-size: .24rem;
        color: #999999;
        line-height: .36rem;
        transform: scale(.75);
        transform-origin: left center;
      }
      .attr{
        margin-bottom: .14rem;
        span{
          color: #666666;
          margin-right: .5rem;
          font-size: .24rem;
        }
      }
      .price{
        .now{
          font-size: .28rem;
          color: $red;
          font-weight: bold;
          margin-right: .1rem;
          i{
            display: inline-block;
            font-style: normal;
            font-size: .24rem;
            transform: scale(.75);
          }
        }
        .old{
          font-size: .24rem;
          color: #ccc;
          text-decoration: line-through;
          margin-right: .1rem;
          i{
            font-style: normal;
          }
        }
        .integral{
          font-size: .24rem;
          color: #666;
        }
      }
      .num_box{
        float: right;
        position: relative;
        width: 1.4rem;
        height: .36rem;
        padding: 0 .4rem;
        margin-top: .14rem;
        margin-right: -.4rem;
        span{
          position: absolute;
          left: 0;
          top: 0;
          width: .3rem;
          height: .3rem;
          line-height: .3rem;
          text-align: center;
          background: $red;
          color: #fff;
          border-radius: 50%;
          font-size: .24rem;
        }
        .add{
          left: auto;
          right: 0;
        }
        input{
          width: 100%;
          height: .36rem;
          border: none;
          border-bottom: 1px solid #CBCBCB;
          vertical-align: top;
          text-align: center;
          font-size: .28rem;
        }
      }
    }
  }
}
.order{
  .main{
    padding: .25rem .20rem;
  }
  .addr{
    align-items: center;
    padding: .2rem;
    margin-bottom: .7rem;
    .lf{
      width: 100%;
      .top{
        .name{
          font-size: .3rem;
          font-weight: bold;
        }
        .phone{
          font-weight: bold;
          font-size: .24rem;
          margin-left: .5rem;
        }
      }
      .detail{
        font-size: .24rem;
        color: #666666;
        margin-top: .18rem;
        line-height: .3rem;
      }
    }
    .rt{
      width: .6rem;
      i{
        font-size: .4rem;
      }
    }
  }
  .shipping{
    align-items: center;
    padding: .2rem;
    margin-bottom: .1rem;
    justify-content: space-between;
    .lf{
      flex-shrink: 0
    }
    .mid{
      width: 100%;
      padding: 0 .3rem;
      line-height: .4rem;
    }
    .rt{
      flex-shrink: 0;
      width: .6rem;
      i{
        font-size: .4rem;
      }
    }
  }
  .shipping_free{
    font-size: .24rem;
    margin-bottom: .3rem;
    span{
      color: $red;
    }
  }
  .time_box{
    padding: .2rem .1rem;
    align-items: center;
    span{
      width: 33.33%;
    }
    .name{
      font-size: .3rem;
    }
    .time, .pos{
      font-size: .24rem;
      color: #929292;
    }
  }
  .insurance{
    padding: .2rem .1rem;
    align-items: center;
    justify-content: space-between;
    margin: .2rem 0;
    .name{
      font-size: .3rem;
    }
  }
  .coupon_box{
    padding: .2rem .1rem;
    align-items: center;
    justify-content: space-between;
    .name{
      flex-shrink: 0;
      font-size: .3rem;
    }
    .rt{
      flex-shrink: 0;
      width: .6rem;
      i{
        font-size: .4rem;
      }
    }
    .mid{
      width: 100%;
      text-align: right;
      padding-right: .3rem;
      font-size: .3rem;
      color: #8C8C8C;
    }
  }
  .coupon_text{
    font-size: .3rem;
    color: #020202;
    span{
      color: $red;
    }
  }
  .summary{
    padding: 0 .1rem;
    margin-top: .3rem;
    .top{
      font-size: .3rem;
      line-height: .8rem;
    }
    ul{
      border-top: 1px solid #EBEBEB;
      border-bottom: 1px solid #EBEBEB;
      li{
        justify-content: space-between;
        font-size: .26rem;
        line-height: .46rem;
      }
    }
    .bottom{
      padding-bottom: .2rem;
      .total{
        justify-content: space-between;
        font-size: .3rem;
        font-weight: bold;
        margin: .1rem;
      }
      .reward{
        font-size: .24rem;
        text-align: right;
        span{
          color: $red;
        }
      }
    }
  }
}
.topay{
  .main{
    padding: .25rem .20rem;
  }
  .select_box{
    label{
      width: 100%;
      justify-content: space-between;
      align-items: center;
      height: 1rem;
      padding: 0 .2rem;
      border-bottom: 1px solid #E6E6E6;
      &:last-child{
        border: none;
      }
      img{
        height: .7rem;
        vertical-align: middle
      }
      p{
        img{
          height: .5rem;
        }
        i{
          font-size: .24rem;
        }
        span{
          vertical-align: middle;
          font-size: .3rem;
          color: #575757;
          margin-left: .2rem;
        }
      }
      .check_box{
        width: .62rem;
        height: .62rem;
        input:checked+span:after{
          left: .22rem;
          top: .1rem;
          width: .18rem;
          height: .28rem;
        }
      }
    }
  }
  .fixed_shop{
    height: 1.2rem;
    .lf{
      .num{
        position: absolute;
        left: .2rem;
        bottom: 1.4rem;
        text-align: left;
      }
      .price{
        font-size: .4rem;
        margin-top: .16rem;
      }
    }
    .btn{
      height: 1rem;
      border-radius: .5rem;
    }
  }
}